// app/page.js
"use client"; // <--- 1. NEEDS TO BE A CLIENT COMPONENT

import { useState } from "react";
import Link from "next/link";
import NavBar from "./components/NavBar";
import ParticlesBackground from "./components/ParticlesBackground"; // <-- Import the background
import { dancingScript } from "./layout"; // <-- Import the font (adjust path if needed)

export default function Home() {
  const [isHoveringMainText, setIsHoveringMainText] = useState(false); // <-- State for particles
  
  // Logic for letter-by-letter hover effect
  const text = "YOUR AURA";
  const letters = text.split(""); 

  return (
    // RECOMMENDATION: Remove the extra <section> with bg-[#141D29] here. 
    // The background should be set in RootLayout.js or in <ParticlesBackground />
    <main> 
      
      {/* 2. PARTICLE BACKGROUND INTEGRATION */}
      {/* The particle effect sits fixed and behind everything else. */}
      <ParticlesBackground isHovering={isHoveringMainText} /> 
      
      {/* The NavBar should sit outside the hero section to be sticky across the whole page. */}
      <NavBar /> 

      <section className="relative overflow-hidden">
        {/* Your light blob effects go here if you still want them */}
        <div className="pointer-events-none absolute inset-0">
          <div className="absolute left-1/2 top-[-200px] h-[500px] w-[900px] -translate-x-1/2 rounded-full bg-white/5 blur-3xl" />
          <div className="absolute left-1/2 top-[-120px] h-[450px] w-[750px] -translate-x-1/2 rounded-full bg-orange-400/10 blur-3xl" />
        </div>

        <div className="relative mx-auto flex max-w-6xl flex-col items-center px-8 pb-24 pt-16 text-center">
          
          <p className="text-[52px] font-semibold tracking-[0.25em] text-[#EE7951] sm:text-[64px]">
            LET'S FEED
          </p>
          
          {/* 3. LETTER-BY-LETTER HOVER IMPLEMENTATION */}
          <div 
            // Set the state when hovering over the main text area
            onMouseEnter={() => setIsHoveringMainText(true)}
            onMouseLeave={() => setIsHoveringMainText(false)}
            // Ensure this container is interactive for the hover events to work
            className="pointer-events-auto" 
          >
            <h1 
              className="mt-3 text-[64px] font-light tracking-[0.08em] sm:text-[86px] md:text-[96px] text-white flex justify-center"
            >
              {letters.map((letter, index) => (
                <span
                  key={index}
                  className={`
                    inline-block           
                    transition-all         
                    duration-150           
                    ease-out               
                    ${letter !== ' ' ? 'hover:scale-125 hover:text-orange-400 hover:rotate-6' : ''}
                  `}
                >
                  {/* Use non-breaking space for layout stability */}
                  {letter === ' ' ? '\u00A0' : letter} 
                </span>
              ))}
            </h1>
          </div>
          {/* END OF HOVER IMPLEMENTATION */}


          <p className="mt-8 max-w-xl text-lg text-white/80">
            A <span className="font-semibold text-white">modern</span> social platform built{" "}
            <span className="font-semibold text-white">for sigmas</span>, by sigmas
          </p>

          <Link
            href="/signup"
            className="mt-10 rounded-full bg-[#50FF6C] px-12 py-3 text-lg font-semibold text-black hover:brightness-110 active:brightness-95"
          >
            Sign up
          </Link>

          {/* optional: just so you can scroll and see navbar effect */}
          <div className="h-[120vh]" />

        </div>

      </section>
      
    </main>
  );
}